import React from 'react';
import './header.css';
import img from '../pictures/logo.png'

class Header extends React.Component {

    render() {


        return (

            <div className="headContainer">
                <img src={img}></img>
                <div></div>

                <div className="speach">
                    <h2>דבר המפקד</h2>
                    <p> ברוכים הבאים לאתר גדוד 21,

                    גדוד מילואים הוא יישות חיה שלובשת ופושטת צורה מעת לעת. בכל גיוס לאימון או תעסוקה מופיעים מאות זוגות רגליים בנעליים גבוהות שהופכות אותו לחי, נושם ובועט חזק. בשאר הזמן הוא נשמה בלבד, אך עם רוח חזקה וערכים.
                    לאורך השנים חיילי הגדוד הוזעקו מביתם ליחידה בכל מבצע צבאי והוטלו היישר למערכה, פעלו היטב מול האויב וניצחו באופן חד משמעי. בחודשים האחרונים נתקלנו באויב בלתי נראה שהשפיע קשות על חיינו האישיים, כלכלית וחברתית.
                    הפעם הגדוד נקרא לפעול למען חייליו. מטרת האתר - לסייע לנפגעים ממגיפת הקורונה ולשוב לשוק העבודה, לסייע לעצמאים לשקם את העסק, ולבסוף לתת ביטוי לרוח הגדוד, למשפחתיות ולערבות ההדדית בין הלוחמים.
                    מודה המון ליוזמים ולעושים במלאכה - ד"ר אשר, ניר אלימלך ובן ברינברג, ומקווה שנצליח לעזור לכם לעבור את התקופה ולהמשיך לנצח כל רגע.
                    </p>
                   <div>סא"ל ארז שלו </div>
                   <div> מפקד הגדוד </div>

                    
                </div>
                <div></div>
            </div>
        )
    }
}

export default Header;