import React, { Component } from 'react';
import { Link, Redirect } from 'react-router-dom';
import './contact.css';

class Contact extends React.Component {



    handleSubmit = (e) => {
        e.preventDefault();
        e.preventDefault();
        let catagories = [];
        catagories.push(e.target.soldierList1.value)
        catagories.push(e.target.soldierList2.value)
        catagories.push(e.target.soldierList3.value)


        let contact = {

            name: e.target.soldierName.value,
            seekObus: e.target.seekOfferBusiness.value,
            list: catagories,
            // photo:  e.target.soldierPhoto.value,
            city: e.target.soldierCity.value,
            area: e.target.soldierArea.value,
            platoon: e.target.soldierPlatoon.value,
            email: e.target.soldierEmail.value,
            phoneNumber: e.target.soldierPhone.value,
            link: e.target.soldierLink.value,
            additional: e.target.details.value,
           

        }
        //////\
        fetch('/contact', {

            method: 'POST',
            body: JSON.stringify(contact),
            headers: {
                'Content-Type': 'application/json'
            }
        }).then(res => {
            this.resetForm()
            console.dir(res)

        }).catch(() => {
            console.log('message not send')
        })

        document.getElementById("format").reset();
        alert('ההודעה נשלחה')
    }


    render() {

        return (
            <div>
                <form id="format" className="form" onSubmit={this.handleSubmit}>
                    <Link to="/"><button className="backButton" > חזרה לעמוד הראשי </button></Link>
                    <h1>צור קשר</h1>
                    <div className="row">
                        <div className="col-25">
                            שם החייל :
                       </div>
                        <div className="col-75"><input name="soldierName" type="text" placeholder="שם" /></div>
                    </div>
                    <div className="row">
                        <div className="col-25">
                            סטטוס:
                        </div>
                        <div className="col-75">
                            <select className="select" name="seekOfferBusiness">
                                <option value="מחפש עבודה">מחפש עבודה</option>
                                <option value="מציע עבודה">מציע עבודה</option>
                                <option value="עסק">עסק</option>
                            </select>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-25">
                            קטגוריות:
                        </div>
                        <div className="col-75">
                            <input name="soldierList1" type="text" placeholder="קטגוריות" />
                            <input name="soldierList2" type="text" placeholder="קטגוריות" />
                            <input name="soldierList3" type="text" placeholder="קטגוריות" />
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-25">
                            עיר:
                    </div>
                        <div className="col-75">
                            <input name="soldierCity" placeholder='עיר' />
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-25">
                            אזור:
                    </div>
                        <div className="col-75">
                            <select className="select" name="soldierArea">
                                <option value="מרכז">מרכז</option>
                                <option value="ירושלים">ירושלים </option>
                                <option value="השרון">השרון</option>
                                <option value="צפון">צפון</option>
                                <option value="דרום">דרום</option>
                            </select>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-25">
                            פלוגה:
                    </div>
                        <div className="col-75">
                            <select className="select" name="soldierPlatoon">
                                <option value="א">א </option>
                                <option value="ב">ב </option>
                                <option value="ג">ג</option>
                                <option value="מסייעת">מסייעת</option>
                                <option value="מפקדה">מפקדה</option>
                            </select>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-25">
                            טלפון:
                    </div>
                        <div className="col-75">
                            <input name="soldierPhone" placeholder='טלפון' />
                        </div>
                    </div>

                    <div className="row">
                        <div className="col-25">
                            אימייל:
                    </div>
                        <div className="col-75">
                            <input
                                name="soldierEmail" placeholder='אימייל' />
                        </div>

                    </div>
                    <div className="row">
                        <div className="col-25">
                            קישור:
                   </div>
                        <div className="col-75">
                            <input
                                name="soldierLink" placeholder='קישור' />
                        </div>
                    </div>
                    <p></p>
                    <div>
                        <textarea name="details" placeholder="פרטים נוספים" cols="30" rows="5" > </textarea>
                        <br></br>
                        <button type="submit" value="שלח טופס">שלח טופס</button>
                    </div>
                </form>

            </div>


        )
    }
}


export default Contact;