import React, { Component } from 'react';
import { BrowserRouter, Route, Switch, Link } from 'react-router-dom';

// import logo from './logo.svg';
import './App.css';

import Card from './view/Card/Card';
import Header from './view/Header/header';
import Popup from './view/popup/popup';
import form from './view/form/form';
import manager from './view/manager/manager';
import contact from './view/contact/contact';


class App extends Component {
  constructor(props) {
    super(props)


    this.state = {
      soldier: [],
      jobSeeker: '',
      jobOffer: '',
      business: '',
      form: true,
      contact: false
    }



    fetch('http://localhost:5000/soldier/getSoldiers', {
      method: 'POST',
      body: JSON.stringify(),
      headers: {
        'Content-Type': 'application/json'
      }
    }).then(res => res.json())
      .then(soldier => {


        this.setState({
          soldier: soldier.data
        })




      }).catch(err => {

      })
  }
  jobSeeker = (e) => {
    if (this.state.jobSeeker == '') {
      this.setState({ jobSeeker: 'מחפש עבודה' })
    } else {
      this.setState({ jobSeeker: '' })
    }
    // console.log(this.state.buttons)
  }
  jobOffer = (e) => {
    if (this.state.jobOffer == '') {
      this.setState({ jobOffer: 'מציע עבודה' })
    } else {
      this.setState({ jobOffer: '' })
    }
    // console.log(this.state.buttons)
  }
  business = (e) => {
    if (this.state.business == '') {
      this.setState({ business: 'עסק' })
    } else {
      this.setState({ business: '' })
    }
    // console.log(this.state.buttons)
  }
  

  render() {
    return (
      <BrowserRouter>
        <div>


          <Popup />
          <Header />
          <Switch>
        
            
            <Route path='/contact' component={contact} />
            <Route path='/manager' component={manager} />
            <div className="App">
              <div className="search">
                {(this.state.business === "") ?
                  <button onClick={this.business}>עסקים </button>
                  : <button style={{ backgroundColor: "black" }} onClick={this.business}>עסקים </button>
                }
                {(this.state.jobOffer === "") ?
                  <button onClick={this.jobOffer}>הצעות עבודה </button>
                  : <button style={{ backgroundColor: "black" }} onClick={this.jobOffer}>הצעות עבודה </button>
                }
                {(this.state.jobSeeker === "") ?
                  <button onClick={this.jobSeeker}>מחפשי עבודה </button>
                  : <button style={{ backgroundColor: "black" }} onClick={this.jobSeeker}>מחפשי עבודה </button>
                }


                {/* <form>
              <input type='submit' value='search' />
              <input type='text' name='searching' />
            </form> */}
                 <Link to="/contact"><button className="contactButton" > צור קשר </button></Link>
               
              </div>
              <header className="App-header">


                <div className='container'>


                  {
                    this.state.soldier.map((element, index) => {
                      if ((this.state.jobSeeker == '') && (this.state.jobOffer == '') && (this.state.business == '')) {
                        return <Card elm={element} />
                      } else {
                        if ((this.state.soldier[index].seekObus == this.state.jobSeeker)
                          || (this.state.soldier[index].seekObus == this.state.jobOffer)
                          || (this.state.soldier[index].seekObus == this.state.business)) {
                          return <Card elm={element} />
                        }
                      }
                    })

                  }


                </div>


              </header>
              <Route path='/form' component={form} />

                        </div>
  
          </Switch>
                
        </div>
      </BrowserRouter>
    );
  }
}
export default App;
